.App {
  text-align: center;
  font-family: 'Open Sans', sans-serif;
}



.App-header {
  background-color: darkkhaki;
  min-height: 5vh;
  display: flex;
  padding: 5px 0;
  margin-top: 10px;
  justify-content: space-around;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-header button, .App-header a {
  width: 20%;
  min-width: fit-content;
  padding: 2px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1.5vmax;
  background-color: inherit;
}


.client {
  display: flex;
  justify-content: space-between;
}

.header {
  padding: 10px 0;
  font-size: 18px;
  font-weight: 500;
  margin: 0;
  background-color: #ccc;
}

.client span {
  display: inline-block;
  width: 25%;
}

.client span:first-of-type {
  text-align: left;
  padding-left: 30px;
  width: 5%;
}

.data_wrap {
  width: 100%;
  overflow: auto;
}

.data {
  min-width: 500px;
}

.dialog__bloking {
  position: absolute;
  border: none;
  width: 300px;
  top: 200px;
  background: #FFFFFF;
  box-shadow: 0px 0px 20px #C5C5C5;
  border-radius: 12px;
  padding: 20px;

}
.dialog__bloking button {
  width: 100px;
  margin: 20px 20px 0;
  cursor: pointer;
  border-radius: 4px;
  border: none;
  padding: 5px;
  color: #FFFFFF;
  background-color: #61dafb;
}
.dialog__bloking p {
  margin: 10px 0 0;
  font-weight: 500;
}

@media screen and (max-width: 480px) {
  body {
    background-color: lightgreen;
  }

  .App-header {
    flex-direction: column;
    height: auto;
    align-items: center;
  }

  .App-header button, .App-header a {
   
   margin: 5px 0;
    
  }

  .client span:first-of-type {
    padding-left: 8px;
  }

  .client {
    font-size: 14px;
    align-items: center;
  }
}